.homeButton, .middleButton{ 
    cursor: pointer;
    height: 43px;
    font-size: 25px;
    white-space: nowrap;
    background-color: rgb(196, 196, 196);
    color: #000000;
    border-color: #293129;
    border-style: solid;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 12px;
    font-family: 'Montserrat', sans-serif;
    transition: 0.2s;
}
.homeButton:hover, .middleButton:hover{
    background-color: rgb(133, 132, 132);
    color: #0c0c0c;
    border-color: #202420;
}
.topBar{
    background-color: #132019;
    position: fixed;
    top:0;
    left:0;
    right:0;
    display:flex;
    flex-direction: row;
    height: 65px;
    padding-left: 30px;
    padding-right: 30px;
    justify-content: space-between;
    transition: 0.2s;
}
.middleButton{
    margin-left:10px; 
    margin-right: 10px;
}
.PSALogo{
    height: 25px;
}
.leftSection{
    display:flex;
    flex: 1;
    align-items:center;
    justify-content: start;
}
.middleSection{
    display:flex;
    flex: 1;
    align-items:center;
    justify-content: center;
}
.rightSection{
    display:flex;
    flex: 1;
    align-items:center;
    justify-content: end;
}