.HomeText {
    background-color: rgb(196, 196, 196);
    font-family: 'Montserrat', sans-serif;
    margin: 0 auto; /* Set left and right margins to 'auto' */
    padding: 20px;
    display: block; /* Block-level element */
    width: fit-content; /* Set the width to the content width */
    text-align: center; /* Horizontally center text within the div */
    border-radius: 16px;
    border: 1px black solid;
    margin-bottom: 50px;
  }
  .HomeText h1{
    font-size: 44px;
  }
  .HomeText p{
    font-size: 18px;
  }
.Home{
    padding-top: 14vh;
    background-size: cover;
    background-image: url('./bn-home-1.jpg');
    padding-bottom: 20px;
}
/* Styles for the ButtonContainer using CSS Grid */
.ButtonContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Adjust the column width as needed */
  gap: 10px; /* Reduce the gap between items */
  justify-items: center;
}

/* Styles for the portCard */
.portCard {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: center;
  padding: 15px;
  width: 400px; /* Set a specific width for all cards */
}

/* Styles for the portImage */
.portImage {
  width: 100%; /* Make all images the same width */
  height: 150px; /* Set a specific height for all images */
  object-fit: cover; /* Crop and center the image */
  border-radius: 4px; /* Add border-radius for a rounded look */
}

/* Styles for the portName */
.portName {
  font-size: 18px; /* Adjust the font size as needed */
  margin: 10px 0; /* Add spacing above and below the name */
}

/* Styles for the portButton */
.portButton {
  cursor: pointer;
  height: 43px;
  font-size: 18px;
  white-space: nowrap;
  background-color: rgba(250, 234, 216, 0.925);
  color: #000000;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-color: #293129;
  border-radius: 12px;
  border-style: solid;
  font-family: 'Montserrat', sans-serif;
  transition: 0.2s;
}

.portButton:hover {
  background-color: rgb(247, 165, 127);
  color: #000000;
}

/* Center the button within the portCard */
.portCardLink {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none; /* Remove underlines from links */
}
